import Vue from 'vue'
import App from './App.vue'
import Navbar from  './components/Navbar.vue'
import weatherapp from './components/weatherapp.vue'
import aboutpage from './components/aboutpage.vue'
import homepage from './components/homepage.vue'
import contactpage from './components/contactpage.vue'
import joinconnect from './components/signin.vue'
import joinconnect1 from './components/signup.vue'
import meetourteam from './components/meetourteam.vue'
import preloader from './components/preloader.vue'
import VueTyperPlugin from 'vue-typer'
import VueRouter from 'vue-router'

Vue.use(VueTyperPlugin)
Vue.config.productionTip = false;
Vue.config.devtools = false;



Vue.use(VueRouter);
const routes = [
    {path: '/weatherapp', name:weatherapp,component: weatherapp},
    {path: '/aboutpage', name:aboutpage,component: aboutpage},
    {path: '/', name:homepage,component: homepage},
    {path: '/contactpage', name:contactpage,component: contactpage},
    {path: '/signin', name:joinconnect,component: joinconnect},
    {path: '/signup', name:joinconnect1,component: joinconnect1},
    {path:'/meetourteam',name:meetourteam,component:meetourteam},
    {path: '/Navbar',name:Navbar,component:Navbar},
   
  {path:'/preloader',name:preloader,component:preloader}
]
const router=new VueRouter({
    routes,
    mode:'history'
});

new Vue({
  el:'#app',
  router,
  render: h => h(App),
}).$mount('#app')
